.App {
  text-align: center;
}

.App-header {
  background-color: #4c0b12;
  min-height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: row; 
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.parallax { 
  background-image: url("./capa.jpg");
  height: 500px; 
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  filter: invert(15%);
}

#globo {
  order: 0;
  font-size: 1.7em;
  position: absolute !important;
  align-self: flex-start;
  opacity: 0;
  margin: 0;
  transition: all 2s;
}

#languagePick {
  order: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  height: 190px;
  transition-duration: 0.5s;
  position: absolute;
  align-self: flex-start;
  margin-top: 5vmin;
}

#languagePick div {
  transition: 0.5s;
  margin: 10px;
}

#languagePick > div:hover {
  cursor: pointer;
  color: slategray;
}

.fa-globe-americas:hover + #languagePick {
  opacity: 1;
}

#languagePick:hover {
  opacity: 1;
}

.bandeiras {
  transition: all 0.2s;
  margin: 0 2vmin 0 2vmin;
}

.bandeiraEspanha, .bandeiraUSA, .bandeiraItalia {
  width: 58px;
}

.bandeiraEspanha:hover, .bandeiraUSA:hover, .bandeiraItalia:hover {
  width: 64px;
}

.bandeiraBrasil{
  width: 54px;
} 

.bandeiraBrasil:hover {
  width: 58px;
}

#frasePrincipal {

  font-size: 50px;
  opacity: 0;
  margin-top: 100px;
  transition: all 2s;
}